main {
    align-items: center !important;

    .container {
        max-width: 500px !important;
    }
}

.main {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #334;
    border-radius: 20px;
    flex-direction: column;

    h2 {
        margin: 0px;
        margin-bottom: 10px;
    }

    a {
        display: flex;
        width: calc(100% - 24px);
        padding: 12px;
        border: 1px solid #556;
        border-radius: 10px;
        cursor: pointer;
        color: #dde;
        text-decoration: none;
        font-size: large;
        margin-top: 10px;
        justify-content: center;
        align-items: center;
        transition: background-color 70ms ease-out;

        svg {
            margin-right: .7ch;
        }
    }
}

body:not(.touch) {
    .main a:hover {
        background-color: #445;
    }
}

body.touch {
    .main a:active {
        background-color: #445;
    }
}